/*APP CSS  */

* {
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

body {
    margin: 0;
    padding: 0;
}

.App {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 1 1;
            flex: 1 1;
    height: 100vh;
    overflow: hidden;
}

.AppHeader {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    border-bottom: 1px solid #ccc;
}

.AppTitle {
    display: -webkit-flex;
    display: flex;
    width: 25vw;
    border-right: 1px solid #ccc;
    -webkit-justify-content: center;
            justify-content: center;
    box-sizing: border-box;
    padding: 20px;
    background-color: #696758;
    color: white;
}

.AppRoom {
    display: -webkit-flex;
    display: flex;
    width: 75vw;
    -webkit-justify-content: center;
            justify-content: center;
    box-sizing: border-box;
    padding: 20px;
    background-color: #D5CFAE;
}

.AppBody {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex: 1 1;
            flex: 1 1;
}

.MessageWrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 75vw;
    -webkit-flex: 1 1;
            flex: 1 1;
    background-color: #F5F0CD;
}

/Message Form / 
.MessageForm {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.MessageInput {
  -webkit-flex: 1 1;
          flex: 1 1;
  padding: 20px;
  border: 0;
  border-top: 1px solid #ccc;
}

.MessageInput:focus {
  outline: none;
}

/MessageList/ 
.MessageList {
  -webkit-flex: 1 1;
          flex: 1 1;
  width: 75vw;
  padding: 20px;
  overflow: auto;
}

.Message {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.MessageRight {
  -webkit-align-items: flex-end;
          align-items: flex-end;
}


.Cloud {
  padding: 10px 25px;
  border-radius: 20px;
  background-color: white;
  display: block;
  margin: 10px 20px 20px 20px;
  border-top-left-radius: 0;
  box-shadow: 1px 1px 2px #444;
  word-break: break-all;
  min-width: 20%;
}

.CloudRight {
  border-radius: 20px;
  border-top-right-radius: 0;
  background-color: rgba(159, 157, 147, 0.86);
  color: white;
}
@media all and (min-width: 480px) {
    .Signup {
      padding: 60px 0;
    }
  
    .Signup form {
      margin: 0 auto;
      max-width: 320px;
    }
  }
  
  .Signup form span.help-block {
    font-size: 14px;
    padding-bottom: 10px;
    color: #999;
  }
